/*------------------------
    Previe Styles  
--------------------------*/
.main-wrapper {
  position: relative;
  background: #ffffff;
}
.prv-header-area {
  padding: 18px 85px;
  padding-top: 18px;
}
.fullscreen {
  &.empty-div {
    @media #{$md-layout} {
      display: none;
    }
    @media #{$lg-layout} {
      display: none;
    }
  }
}

.with-particles {
  position: relative;
  z-index: 3;
  .frame-layout__particles {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    @media #{$large-mobile} {
      display: none;
    }
  }
  .inner {
    position: relative;
    z-index: 3;
  }
}

.gradient-overlay {
  position: relative;
}

.gradient-overlay::before {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  content: '';
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#000000+7,000000+100&0+50,0.5+100 */
  background: -moz-linear-gradient(
    -45deg,
    rgba(0, 0, 0, 0) 7%,
    rgba(0, 0, 0, 0) 50%,
    rgba(0, 0, 0, 0.5) 100%
  ); /* FF3.6-15 */
  background: -webkit-linear-gradient(
    -45deg,
    rgba(0, 0, 0, 0) 7%,
    rgba(0, 0, 0, 0) 50%,
    rgba(0, 0, 0, 0.5) 100%
  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    135deg,
    rgba(0, 0, 0, 0) 7%,
    rgba(0, 0, 0, 0) 50%,
    rgba(0, 0, 0, 0.5) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#80000000',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

.prv-banner-wrapper {
  min-height: 100vh;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  display: flex;
  align-items: center;
  position: fixed;
  width: 100%;
  top: 0px;
  left: 0px;
  @media #{$sm-layout} {
    min-height: auto;
    position: static;
    padding: 60px 0;
    background-image: none !important;
    background-color: #f6f6f6;
  }
  @media #{$md-layout} {
    min-height: auto;
    position: static;
    padding: 80px 0;
    background-image: none !important;
    background-color: #f6f6f6;
  }
  @media #{$lg-layout} {
    min-height: auto;
    position: static;
    padding: 80px 0;
    background-image: none !important;
    background-color: #f6f6f6;
  }
  @media #{$md-layout} {
  }
  @media #{$lg-layout} {
  }
  .inner {
    padding: 35px;
    position: relative;
    z-index: 1;
    @media #{$laptop-device} {
      padding: 35px 0;
    }
    @media #{$sm-layout} {
      padding: 0;
    }
    .logo {
      img {
        max-height: 70px;
      }
    }
    .title {
      font-size: 23px;
      color: #515462;
      line-height: 42px;
      margin: 30px 0 50px 0;
      @media #{$laptop-device} {
        font-size: 20px;
        line-height: 36px;
        margin: 24px 0 40px 0;
      }
      @media #{$sm-layout} {
        margin: 32px 0 20px 0;
      }
    }
    .purshase-btn {
      margin-left: -15px;
      margin-right: -15px;
      @media #{$laptop-device} {
        margin-left: -10px;
        margin-right: -10px;
      }
      @media #{$sm-layout} {
        margin-left: -5px;
        margin-right: -5px;
      }
      a {
        margin-left: 15px;
        margin-right: 15px;
        @media #{$sm-layout} {
          margin-left: 5px;
          margin-right: 5px;
        }
        &.rn-button-style--2 {
          @media #{$laptop-device} {
            padding: 10px 25px;
          }
          @media #{$sm-layout} {
            padding: 9px 12px;
            font-size: 14px;
          }
        }
        @media #{$laptop-device} {
          margin-left: 10px;
          margin-right: 10px;
        }
        & + a {
          @media #{$small-mobile} {
            margin-top: 10px;
          }
        }
      }
    }
  }
}

/*---------------------
    Demo Area  
-----------------------*/

.home-demo-area {
  .wrapper {
    @media #{$md-layout} {
      padding: 0 30px !important;
    }
    @media #{$sm-layout} {
      padding: 0 30px !important;
    }
  }
  .section-title {
    &.text-white {
      h2 {
        color: #c6c9d8;
        margin-bottom: 9px;
      }
