// Start Single Demo
.bg_color--7 {
  .single-demo {
    position: relative;
    a {
      > span {
        display: block;
        text-align: center;
        padding: 20px 0;
        color: rgba(255, 255, 255, 0.7);
        font-size: 20px;
        font-weight: 500;
        transition: 0.3s ease-in-out;
        letter-spacing: 0.2px;
      }
    }
  }
}

.single-demo {
  padding: 0 30px;
  margin: 45px 0;

  @media #{$laptop-device} {
    padding: 0px;
  }
  @media #{$md-layout} {
    padding: 0px;
  }
  @media #{$sm-layout} {
    padding: 0px;
    margin: 20px 0;
  }
  @media #{$lg-layout} {
    padding: 0px;
  }
  a {
    display: block;
    @extend %transition;
    position: relative;
    z-index: 2;
    img {
      box-shadow: 3px 4px 20px 0 rgba(0, 0, 0, 0.1);
      transition: 0.7s cubic-bezier(0.26, 0.76, 0.3, 0.75);
      width: 100% !important;
      border-radius: 6px;
    }
    .title {
      display: block;
      text-align: left;
      padding: 25px 0 10px 0;
      color: #1f1f25;
      font-size: 20px;
      font-weight: 500;
      transition: 0.3s ease-in-out;
      letter-spacing: 0.2px;
      margin: 0;
    }
    .info {
      display: block;
      padding: 0;
      color: rgba(29, 29, 36, 0.75);
      font-size: 14px;
      font-weight: 300;
      transition: 0.3s ease-in-out;
      line-height: 25px;
      @media #{$sm-layout} {
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }
    }
    > span {
      &.number {
        display: none;
        position: absolute;
        color: #f6f6f6;
        z-index: -1;
        font-size: 81px;
        bottom: -55px;
        line-height: inherit;
        font-weight: 300;
        letter-spacing: 11px;
      }
    }
    &:hover {
      img {
        -webkit-transform: translate3d(0, -10px, 0);
        transform: translate3d(0, -10px, 0);
        box-shadow: 0 50px 80px -10px rgba(0, 0, 0, 0.17);
      }
    }
    .label-new {
      position: absolute;
      top: 5px;
      background: #f81f01;
      background: linear-gradient(145deg, #f81f01 0%, #ee076e 100%);
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f81f01', endColorstr='#ee076e',GradientType=1 );
      padding: 9px 14px;
      color: #fff;
      border-radius: 4px;
      font-size: 14px;
      line-height: 1;
      right: 5px;
      text-transform: uppercase;
      letter-spacing: 0.5px;
      font-weight: 500;
    }
  }
}

/*-------------------
Faq Area  
----------------------*/
.pv-feaq-area {
  .accodion-style--1 .accordion__item + .accordion__item {
    margin-top: 20px;
  }
  .accodion-style--1 .accordion__item .accordion__heading .accordion__button {
    font-size: 18px;
    padding: 10px 0;
  }
  .accodion-style--1 .accordion__item .accordion__heading {
    width: 100%;
  }
  .accodion-style--1
    .accordion__item
    .accordion__heading
    .accordion__button::after {
    width: 100%;
  }
  .accodion-style--1
    .accordion__item
    .accordion__heading
    .accordion__button::after {
    height: 1px;
  }

  .accordion__panel {
    p {
      font-size: 16px;
      line-height: 30px;
      background: none;
      border: none;
      padding: 0 0 15px;
      margin: 0;
      font-weight: 400;
      padding-top: 11px;
      color: #1d1d24;
      font-family: 'Cormorant Garamond', serif;
    }
  }
}

/*-------------------
    Tab BUtton  
---------------------*/

.tablist-inner {
  display: inline-block;
}
.pv-tab-button {
  display: flex;
  justify-content: center;
  list-style: none;
  background: rgba(255, 26, 80, 0.03);
  min-width: 570px;
  margin: 0 auto;
  border-radius: 6px;
  margin-bottom: 30px;
  margin-top: 35px;
  flex-wrap: wrap;
  padding: 0;
  padding: 0 36px;
  outline: none;
  @media #{$sm-layout} {
    margin-bottom: 18px;
    margin-top: 5px;
    padding: 8px 15px;
  }
  @media #{$md-layout} {
    padding: 0 15px;
  }
  @media #{$sm-layout} {
    min-width: auto;
  }
  .label {
    position: relative;
    &::after {
      position: absolute;
      top: 0;
    }
  }
  li {
    padding: 0 14px;
    color: #101010;
    font-size: 16px;
    cursor: pointer;
    outline: none;
    @media #{$large-mobile} {
      padding: 0 11px;
    }
    span {
      position: relative;
      display: block;
      padding: 20px 0;
      cursor: pointer;
      @media #{$large-mobile} {
        padding: 11px 0;
      }
      &::after {
        position: absolute;
        left: 0;
        background: $theme-color;
        bottom: 0;
        height: 3px;
        content: '';
      }
    }
    &.react-tabs__tab--selected {
      span {
        color: $theme-color;
        &::after {
          width: 100%;
        }
      }
    }
  }
}

/*-----------------------
Footer Style  
-------------------------*/
.prv-footer {
  .inner {
    h2 {
      &.title {
        color: $heading-color;
        font-size: 60px;
        font-weight: 500;
        margin-bottom: 65px;
      }
    }
  }
}

a {
  &.purchase-btn {
    display: inline-block;
    padding: 0 75px;
    height: 70px;
    line-height: 70px;
    border-radius: 43px;
    font-size: 18px;
    font-weight: 500;
    text-transform: uppercase;
    transition: 0.4s ease-in-out;
    color: #ffffff;
    background: $heading-color;
    border: 1px solid $heading-color;
    &:hover {
      color: $heading-color;
      border: 1px solid $heading-color;
      background: transparent;
    }
  }
}

/* Feature Area */
.prv-feature {
  .content {
    h3 {
      &.title {
        color: #c6c9d8;
        @media #{$md-layout} {
          margin-bottom: 13px;
          font-size: 20px;
        }
        @media #{$sm-layout} {
          margin-bottom: 13px;
          font-size: 20px;
        }
      }
    }
    p {
      &.subtitle {
        color: #c6c9d8;
        @media #{$md-layout} {
          font-size: 16px;
          line-height: 28px;
        }
        @media #{$laptop-device} {
          font-size: 16px;
          line-height: 26px;
        }
        @media #{$sm-layout} {
          font-size: 16px;
          line-height: 28px;
          color: #ffffff;
        }
      }
    }
  }
}

.text-white-wrapper {
  a {
    &.rn-button-style--2 {
      border: 2px solid #fff;
      color: #fff;
    }
  }
}

/*---------------------------
    Responsive Device Area  
-----------------------------*/

.responsive-device-area {
  padding: 293px 0;
  @media #{$laptop-device} {
    padding: 162px 0;
    background-position: 7%;
    margin-top: 0;
  }
  @media #{$lg-layout} {
    padding: 120px 0;
    background-image: none !important;
    background-color: #101010;
    margin-top: 0;
  }
  @media #{$md-layout} {
    padding: 80px 0;
    background-image: none !important;
    background-color: #101010;
    margin-top: 0;
  }
  @media #{$sm-layout} {
    padding: 80px 0;
    background-image: none !important;
    background-color: #101010;
    margin-top: 0;
  }

  .inner {
    padding-left: 130px;
    padding-bottom: 20px;
    @media #{$lg-layout} {
      padding-left: 80px;
      padding-bottom: 0;
      padding-right: 80px;
    }
    @media #{$md-layout} {
      padding-left: 50px;
      padding-bottom: 0;
      padding-right: 50px;
    }
    @media #{$sm-layout} {
      padding-left: 0;
      padding-bottom: 0;
      padding-right: 0;
    }
  }
  .section-title {
    .title {
      font-size: 54px;
      line-height: 70px;
      margin-bottom: 20px;
      @media #{$md-layout} {
        font-size: 45px;
        line-height: 61px;
      }
      @media #{$sm-layout} {
        font-size: 30px;
        line-height: 50px;
      }
    }
    p {
      font-size: 16px;
      padding: 0;
      padding-right: 6%;
      margin-bottom: 0;
      color: rgba(198, 201, 216, 0.75);
    }
  }

  .responsive-layout-mokup {
    display: flex;
    flex-wrap: wrap;
    margin-top: 40px;
    margin-bottom: -20px;

    .mokup-list {
      border: 2px solid rgba(255, 255, 255, 0.2);
      border-radius: 5px;
      width: 100px;
      height: 100px;
      margin-right: 20px;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-bottom: 20px;
      @media #{$sm-layout} {
        width: 90px;
        margin-right: 15px;
      }
      .icon {
        margin-bottom: 15px;
        padding-top: 5px;
      }
      h6 {
        color: rgba(198, 201, 216, 0.75);
        font-size: 14px;
        margin-bottom: 0;
        font-weight: 300;
      }
    }
  }
  .subtitle {
    color: rgba(198, 201, 216, 0.75);
    font-size: 12px;
    margin-top: 35px;
    margin-bottom: 0;
    letter-spacing: 1px;
  }
}

