/*--------------------------
    Card Here  
-----------------------------*/

.card-box {
  background: #ffffff;
  padding: 41px 39px;
  box-shadow: 0 25px 65px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  @media #{$lg-layout} {
    padding: 30px 27px;
  }
  @media #{$md-layout} {
